import React, { useEffect, useState } from "react";
import "./index.css";
import toast from "react-hot-toast";
import axios from "axios";
import TodoForm from "./components/Form/TodoForm";
import { Button, Modal } from "antd";

const App = () => {
  const [todo, setTodo] = useState([]);
  const [title, setTitle] = useState();
  const [visible, setVisible] = useState(false);
  const [selected, setSelected] = useState(null);
  const [updateTitle, setUpdateTitle] = useState("");
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  //handle form
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/v1/todo/create-todo", {
        title,
      });
      if (data?.success) {
        toast.success(`${title} is created`);
        getAllTodo();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong in input form");
    }
  };

  //get all todo
  const getAllTodo = async () => {
    try {
      const { data } = await axios.get(`/api/v1/todo/get-todo`);
      if (data?.success) {
        setTodo(data?.todo); //todo dari gel all todo controller
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong in getting category");
    }
  };

  useEffect(() => {
    getAllTodo();
    // getAllStatus();
  }, []);

  //update todo
  const handleUpdate = async (e, value) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(
        `/api/v1/todo/update-todo/${selected._id}`,
        { title: updateTitle }
      );
      if (data?.success) {
        toast.success(`${updateTitle} is updated`);
        setSelected(null);
        setUpdateTitle("");

        setVisible(false);
        getAllTodo();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  };

  // // //change status
  // const handleChange = async (value) => {
  //   try {
  //     const { data } = await axios.put(
  //       `/api/v1/todo/update-todo/${selected._id}`,
  //       {
  //         status: value,
  //       }
  //     );
  //     if (data.success) {
  //       toast.success(`${updateStatus} is updated`);
  //       setSelected(null);
  //       setUpdateStatus("");
  //       // setChangeStatus("");
  //       setVisible(false);
  //       getAllTodo();
  //     } else {
  //       toast.error(data.message);
  //     }
  //   } catch (error) {
  //     console.log(error);
  //     toast.error("Something went wrong");
  //   }
  // };

  //delete todo
  const handleDelete = async (pId) => {
    try {
      const { data } = await axios.delete(`/api/v1/todo/delete-todo/${pId}`);
      if (data.success) {
        toast.success(`${title} is deleted`);
        getAllTodo();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Something went wrong");
    }
  };

  // //get all status
  // const getAllStatus = async () => {
  //   try {
  //     const { data } = await axios.get("/api/v1/todo/get-status");
  //     setGetStatus(data);
  //   } catch (error) {
  //     console.log(error);
  //   }
  // };

  return (
    <div className="flex justify-center items-center w-full bg-darkPurple h-taskList  font">
      <div>
        <div className="card h-card" style={{ width: "30rem" }}>
          <h5 className=" card-title text-center">List Your Todo</h5>
          <div>
            <ul className="list-group list-group-flush ">
              <div className="w-100">
                <table className="table ">
                  <thead>
                    <tr>
                      <th scope="col">Todo</th>
                      <th scope="col"></th>
                      <th scope="col">Update</th>
                      <th scope="col">Delete</th>
                    </tr>
                  </thead>
                  <tbody>
                    {todo?.map((c) => (
                      <>
                        <tr>
                          <td key={c._id}>{c.title}</td>

                          <td>
                            {/* <button className="btn btn-primary btn-sm">
                              Status
                            </button> */}
                            {/* <Select
                              bordered={false}
                              onChange={(value) => handleUpdate(c._id, value)}
                              defaultValue={c?.status}
                              setUpdateStatus
                            >
                              {status?.map((status, i) => (
                                <Option key={i} value={status}>
                                  {status}
                                </Option>
                              ))}
                            </Select> */}
                          </td>
                          <td>
                            <button
                              className="btn btn-primary btn-sm"
                              onClick={() => {
                                setVisible(true);
                                setUpdateTitle(c.title);
                                setSelected(c);
                              }}
                            >
                              Update
                            </button>
                          </td>
                          <td>
                            <button
                              className="btn btn-danger btn-sm "
                              onClick={() => {
                                handleDelete(c._id);
                              }}
                            >
                              Delete
                            </button>
                          </td>
                        </tr>
                      </>
                    ))}
                  </tbody>
                </table>
              </div>
              <Modal
                onCancel={() => setVisible(false)}
                footer={null}
                visible={visible}
              >
                <TodoForm
                  value={updateTitle}
                  setValue={setUpdateTitle}
                  handleSubmit={handleUpdate}
                />
              </Modal>
            </ul>
            <div className="flex items-center justify-center">
              <Button type="primary" onClick={showModal}>
                Add Your Task
              </Button>
              <Modal
                title="Add Your Task"
                open={isModalOpen}
                onOk={handleOk}
                onCancel={handleCancel}
              >
                <TodoForm
                  handleSubmit={handleSubmit}
                  value={title}
                  setValue={setTitle}
                />
              </Modal>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default App;
